@import url(https://fonts.googleapis.com/css?family=Montserrat);
.style_event-card__2eDzd {
    position: relative;
    height: 10vw;
    width: 35vw;
    border-radius: 5px;
    color: black;
    z-index: 0;
    display: flex;
    background-color: #f5f5f5;
    padding: 2.5vw;
    box-shadow: 0 8px 20px -6px black;
}

.style_event-card__2eDzd:hover {
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
            transition-timing-function: ease-out;
    -webkit-transform: translateY(-7px);
            transform: translateY(-7px);
    box-shadow: 0 14px 30px -6px black;
}

.style_event__1N3ih {
    width: 35vw;
    display: grid;
    grid-template-rows: 30% 60%;
}

.style_event-name__1boEy {
    font-size: 1.85vw;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;

}

.style_event-description__1cWZn {
    font-size: 1.3vw;
    font-family: 'Montserrat', sans-serif;

}

/* logos */

.style_logos__2i-Rw {
    max-width: 6vw;
    padding-right: 1.5vw;
}

/* mobiles devices */

@media only screen and (max-width: 480px) {
    .style_event-card__2eDzd {
        height: 30vw;
        width: 70vw;
        margin: 1vh 0vh;
        padding: 2vh;
    }
    .style_event__1N3ih {
        width: 100%;
        height: 10vh;
    }
    .style_event-name__1boEy {
        font-size: 3.25vw;
    }
    .style_event-description__1cWZn {
        font-size: 3vw;
    }
    .style_logos__2i-Rw {
        max-width: 13vw;
        padding-right: 2vw;
    }
}
